import { ApiError } from '../model/api_error';

// const END_POINT = 'http://127.0.0.1:5000/';
const END_POINT = "http://azi.eu-4.evennode.com/"

const LAST_SERVICE_LIMIT = 3;


class Api {
    
    static async getServices(type) {
        try {
            const response = await Api._fetchAllServices();
            return response.filter(value => type === value.serviceType);
        } catch (err) {
            throw err;
        }
    }

    static async getLastServices() {
        try {
            const response = await Api._fetchAllServices();
            return response.reverse().filter((_, index) => index + 1 <= LAST_SERVICE_LIMIT);
        } catch (err) {
            throw err;
        }
    }

    static async getCourselData() {
        return new Promise((resolve, reject) => {
            fetch("data/carousel_data.json")
                .then(data => data.json())
                .then(data => resolve(data.data))
                .catch(err => reject(err));
        });
    }

    static async _fetchAllServices() {
        return new Promise((resolve, reject) => {
            fetch("data/services.json")
                .then(data => data.json())
                .then(data => resolve(data.services))
                .catch(err => reject(err));
        });
    }

    static async sendMail(body) {
        try{
            const response = await fetch(END_POINT + 'api/send-mail', {
                method: "POST",
                body: body
            });
            if(!response.ok)
                throw new ApiError(response.status, await response.text());
        }catch(error){
            throw error;
        }
    }

    
}

export { Api };