import React from 'react';
import { Link } from '../../services/Link';
import MenuIcon from '@material-ui/icons/Menu';
import { SideBar } from './side_bar';
import './NavBar.css';

const menuItems = [
    {
        path: "/",
        label: "Accueil"
    }, {
        path: "/service",
        label: "Service"
    }, {
        path: "/about",
        label: "A Propos"
    }, {
        path: "/contact",
        label: "Nous contacter"
    }
];


class NavBar extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            show: false,
            sideBarState: false
        };

        if (props.controller) {
            props.controller.open = this.open;
            props.controller.close = this.close;
        }

    }

    open = () => {
        this.setState({ show: true });
    }

    close = () => {
        this.setState({ show: false });
    }
    
    closeSideBar = () => {
        this.setState({ sideBarState: false });
    }
    

    render() {
        const path = window.location.pathname;
        return <>
            <div className="nav-bar-container">
                <div className="logo">
                    <img src={process.env.PUBLIC_URL + "/logo.png"} alt="logo du site" style={{height:'48px'}}/>
                </div>
                <div className='navigation'>
                    {
                        menuItems.map((value, index) => {
                            return <Link key={index} to={value.path}>
                                <div className={'li' + (path === value.path ? " selected" : "")}>
                                    <p>{value.label}</p>
                                </div>
                            </Link>
                        })
                    }
                </div>
                <div className="mobile-menu-icon" onClick={() => this.setState({ sideBarState: true })}>
                    <MenuIcon />
                </div>
            </div>
            <div  className="nav-bar-container-height"></div>

            {
                this.state.sideBarState && <SideBar close={this.closeSideBar} />
            }

        </>
    }

};

export { NavBar };