import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import './PrestationCard.css';
import SpringModal from '../modal/ModalImg';
import { CardActions } from '@material-ui/core';

const useStyles = makeStyles({
  root: {
    width: 345,
  },
  media: {
    height: 140,
  },
});

export default function PrestationCard({ project }) {
  const classes = useStyles();
  return <div>
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={project['imageUrl']}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">{project.title}</Typography>
          <Typography variant="body2" color="textSecondary" component="p">{ project.description }</Typography>
        </CardContent>
      </CardActionArea>
      <CardActions><SpringModal project={project}/></CardActions>
      
    </Card>
  </div>
}
