import { CircularProgress, FormControl, MenuItem, Select } from '@material-ui/core';
import React from 'react';
import PrestationCard from '../components/PrestationCard/PrestationCard';
import { prestationState } from '../data/prestation_state';
import { Api } from '../services/Api';
import CategoriesPrestations from './CategoriesPrestations';
import './Prestations.css'

class Prestations extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            loading: true,
            error: false,
            categoryIndex: 0,
            projects: null,
        };

        this.categories = ['LOCATION ENGIN LOURD', 'LOCATION VEHICULE', 'VENTE'];
    }

    componentDidMount() {
        this._getProject(this.state.categoryIndex);
    }

    _getProject(index) {
        let type = null;

        if (index === 0)
            type = "engin-lourd";
        else if(index === 1){
            type = "vehicule";
        }else if(index === 2){
            type = 'vente'
        }

        Api.getServices(type).then(data => {
            this.setState({loading: false, projects: data})
        }).catch(_ => {});
    }

    onChanged = index => { // quand le category change
        console.log(index)
        // console.log(this.categories[index]);
        this.setState({ loading: true, categoryIndex: index });
        this._getProject(index);
    }

    render() {

        if (this.state.loading) {
            return <div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <CircularProgress />
            </div>
        }

        const projects = this.state.projects;
        // console.log(projects);

        return <>
            <CategoriesPrestations index={this.state.categoryIndex} categories ={this.categories} onChanged={this.onChanged} />
            <div className="mobile-select-category">
                <FormControl variant="outlined">
                    <Select value={this.state.categoryIndex} categories ={this.categories} onChange={e => this.onChanged(e.target.value)}>
                        {prestationState.map(({label, value}) => <MenuItem key={value} value={value}>{label}</MenuItem>)}
                    </Select>
                </FormControl>

            </div>
            <div id='scrollable-container'>
                <div id='projets-card-conatiner'>
                    {projects.map((project, index) => <PrestationCard key={index} project={project}/>)}
                </div>
            </div>

        </>
    }
}

export { Prestations }