import { Close } from "@material-ui/icons";
import React from "react";
import { Link } from "../../services/Link";
import "./side_bar.css";

const SideBar = React.memo((props) => {
    return <div className="side-bar">

        <div className="side-bar-header">
            <div className="logo"><img src={process.env.PUBLIC_URL + "/logo.png"} alt="logo du site" style={{height:'48px'}}/></div>
            <div><Close onClick={props.close} style={{color:"FFF", fontSize: "30px"}}/></div>
        </div>

        <div className='navigation-mobile'>
            <Link to='/'>
                <div className='li-mobile'><p>Accueil</p></div>
            </Link>
            <Link to='/service'>
                <div className='li-mobile'><p>service</p></div>
            </Link>
            <Link to='/about'>
                <div className='li-mobile'><p>A Propos</p></div>
            </Link>
            <Link to='/contact'>
                <div className='li-mobile'><p>Nous contacter</p></div>
            </Link>
        </div>
    </div>
});

export { SideBar };