import React from 'react';
import MailIcon from '@material-ui/icons/Mail';
import PhoneIcon from '@material-ui/icons/Phone';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import { Button,  CircularProgress,  InputAdornment, TextField } from '@material-ui/core';
import { Person, Send } from '@material-ui/icons';
import { Api } from '../services/Api';
import './Contact.css';


class Contact extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            nom: "",
            mail: "",
            description: "",
            loading: false,
            nameMessageError: null,
            mailMessageError: null,
            descriptionMessageError: null
        }
        
    }

    onNameChange = (e) => {
        this.setState({
           nom: e.target.value,
           nameMessageError: null
        })
    }
    onMailChange = (e) => {
        this.setState({
            mail: e.target.value,
            mailMessageError: null
        })
    }

    onDescriptionChange = (e) =>{
        this.setState({
            description: e.target.value,
            descriptionMessageError: null
        })
    }

    onSubmit = () => {
        if(this.state.loading)
            return;
        const nom = this.state.nom;
        const mail = this.state.mail;
        const description = this.state.description;

        

        if(nom.trim() === "") {
            this.setState({ nameMessageError: "Entrez votre nom" })
            return;
        }
        if(mail.trim() === "") {
            this.setState({mailMessageError: "Entrez votre email"})
            return;
        }

        if(description.trim() === "") {
            this.setState({descriptionMessageError: "Entrez un message"})
            return;
        }

        this.setState({ loading: true });

        const formData = new FormData();
        formData.append("name", nom);
        formData.append("mail", mail);
        formData.append("description", description);

        Api.sendMail(formData).then(_ => {
            this.setState({ loading: false });
        }).catch(err => {
            console.error(err);
        });
    }

    render() {
        return <div id="contact-container"  >
            <div className="contact-actions">
                <div className="icon"><PhoneIcon htmlColor="#fff" /> <p>(+226) 79247431</p></div>
                <div className="icon"><WhatsAppIcon htmlColor="#fff" /> <p>(+226) 64709696</p></div>
                <div className="icon"><MailIcon htmlColor="#fff" /><p>srdmouaga@gmail.com</p></div>      
            </div>
            <div className="content">     
                <TextField 
                    label="Nom" 
                    variant="outlined" 
                    value= {this.state.nom}
                    onChange= {this.onNameChange}
                    error={this.state.nameMessageError != null}
                    helperText={this.state.nameMessageError}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <Person />
                            </InputAdornment>
                        ),
                    }}
                />
                <TextField 
                    label="Mail" 
                    variant="outlined"
                    value= {this.state.mail} 
                    onChange= {this.onMailChange}
                    error = {this.state.mailMessageError != null}
                    helperText = {this.state.mailMessageError}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <MailIcon />
                            </InputAdornment>
                        ),
                    }}
                />             
                <TextField
                    label="Message"
                    variant="outlined"
                    value= {this.state.description}
                    onDescriptionChange= {this.onDescriptionChange}
                    error = {this.state.descriptionMessageError != null}
                    helperText ={this.state.descriptionMessageError}
                    rows={4}
                    multiline
                />

                <Button
                    onClick={this.onSubmit}
                    variant="contained"
                    color="primary"
                    style={{ height: '48px' }}>
                    {this.state.loading ? <CircularProgress style={{ color: '#FFF' }} /> : <><span style={{ marginRight: '5px' }}>Envoyer</span><Send/></>}
                </Button>
            </div>
        </div>
    }
}

export { Contact };