import React from 'react';

const CategoriesPrestations = React.memo(({ categories, index, onChanged }) => {

    const onClick = index => _ => {
        onChanged(index);
        
    };

    return <>
        <div id='categories' >
            {categories.map((categorie, i) => <p key={i} className={`category${i === index ? ' selected' : ''}`} onClick={onClick(i)}>{categorie}</p>)}
        </div>
        <div id='categories-height'></div>
    </>

});

export default CategoriesPrestations;