
import React from 'react';
import './Carousel.css';

// FIXME: ecouter windows resize et redimener on fonction
class Carousel extends React.Component {

    constructor(props) {
        super(props);
        // aspectRatio: 4/3
        // style

        this.state = {
            containerHeight: 0,
            currentIndex: 0
        };

        // on garde la reference pour calucler le ration apect
        this.containerRef = React.createRef();

        this.timeId = null;
        this.length = this.props.children.length;
    }

    componentDidMount() {
        // calculer le ratio aspect
        const container = this.containerRef.current;
        this.setState({ containerHeight: container.offsetWidth / this.props.aspectRatio });

        // setTime
        this.timeId = setInterval(this._handleChange, 5000);
    }

    _handleChange = () => {
        this.setState(state => ({
            currentIndex: (state.currentIndex + 1) % this.length
        }));
    }

    componentWillUnmount() {
        if (this.timeId)
            clearInterval(this.timeId);
    }

    render() {
        const children = this.props.children;
        return <div className='carousel-container' style={{height: this.state.containerHeight, ...this.props.style}} ref={this.containerRef}>
            
            {React.Children.map(children, (child, index) => {

                const currentIndex = this.state.currentIndex;
                const prevIndex = currentIndex === 0 ? this.length - 1 : currentIndex - 1;

                if (index === currentIndex)
                    return React.cloneElement(child, { className: 'slideIn' });

                if (index === prevIndex)
                    return React.cloneElement(child, { className: 'slideOut' });

                return child;
            })}

            <div className='indicator'>
                { children.map((_, index) => <span key={index} className={this.state.currentIndex === index ? 'selected' : undefined}></span>) }
            </div>

        </div>;
    }

}

export default Carousel;