import React from 'react';
import RouterInstance from '../services/Router';
import { changePageTitle } from '../services/change_page_title';
import HomePage from '../home/HomePage';
import PrestationsPage from '../prestations/PrestationsPage.jsx';
import ContactPage from '../contact/ContactPage';
import AboutPage from '../About/AboutPage';

class HomeHandler extends React.PureComponent{

    constructor(props){
        super(props);

        const path = window.location.pathname;
        const args = window.location.hash;

        changePageTitle(path);

        this.state = {
            path,
            args: args === '' ? '':args.substr(1, args.length)
        };
    }

    componentDidMount() {
        RouterInstance.onGenerate((path, args) => {
            changePageTitle(path);
            this.setState({ path, args});
        });
    }

    render() {
        // eslint-disable-next-line default-case
        switch(this.state.path) {
            case '/':
                return <HomePage />
            case '/service':
                return <PrestationsPage />
            case '/contact':
                return <ContactPage />
            case '/about':
                return <AboutPage />
        }
    }
}

export { HomeHandler };