import { CircularProgress } from '@material-ui/core';
import React from 'react';
import Carousel from '../components/Carousel/Carousel';
import CarouselItem from '../components/Carousel/CarouselItem';
import { Api } from '../services/Api';
import './Home.css';
import PrestationCard from '../components/PrestationCard/PrestationCard';


class Home extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            categoryIndex: 0,
            error: true,
            services: null,
            carouselData: []
        }
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchData() {

        Api.getCourselData().then(data => {
           this.setState({ carouselData: data });
        }).catch(_ => {});

        Api.getLastServices().then(data => {
            this.setState({ loading: false, services: data });
        }).catch(_ => {});
    }

    onChanged = () => {
        this.setState({ loading: true });
        this.fetchData();
    }

    render() {

        if (this.state.loading) {
            return <div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <CircularProgress />
            </div>
        }

        const services = this.state.services;

        return <>
            <div className="test">
                <Carousel aspectRatio={16/7} style={{ width: '100%',margin: 'auto', backgroundColor:'#000' }}> 
                    {
                         this.state.carouselData.map((data, index) => <CarouselItem key={index}>
                            <img src={data.imageUrl} alt='' />
                            <div className='carousel-label'>
                                <p>{data.title}</p>
                                <p>{data.type}</p>
                            </div>
                        </CarouselItem>)
                    }
                </Carousel>
            </div>
            <div id='scrollable-container'>
                <div id='projets-card-conatiner'>
                    {services.map((project, index) => <PrestationCard key={index} project={project}/>)}
                </div>
              
            </div>
        </>
    }
}

export { Home };